import React from "react"
import { graphql } from "gatsby"
import Layout from "../components/layout"

export const query = graphql `
    query ServiceItemQuery($slug: String!) {
        servicesDataJson(slug: {eq: $slug}) {
            id
            slug
            icon
            title
            text
        }
    }
    
`;

export default ({ data }) => {
    const service = data.servicesDataJson;

    return (
        <Layout>
            <section className="g-headline">
                <div className="container">
                    <h1 className="g-headline__title">{service.title}</h1>
                    <p className="g-headline__text">Short description, with large sense</p>
                </div>
            </section>

            <section className="service container">
                <p className="service__text">{service.text}</p>
            </section>
        </Layout>
    )
}